import { model, Schema, models } from 'mongoose';

export default function getbreakagesModel() {
  const collectionName = 'breakages';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const breakagesSchema = new Schema({
    productCode: {
      type: String,
      required: true,
    },
    brandCode: {
      type: String,
      required: true,
    },
    sizeCode: {
      type: String,
      required: true,
    },
    productName: {
      type: String,
      required: true,
    },
    date: {
      type: Date,
      required: true,
    },
  });

  return model(collectionName, breakagesSchema);
}
